.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#gameboard-container {
  margin: auto;
  max-width: 800px;
}
table {
  /* display: block; */
  margin: auto;
}

td {
  background-color: #aad751; /*lighter green from google minesweeper*/
  width: 40px;
  height: 40px;
  font-size: 30px;
}

.cell {
  width: 40px;
  height: 40px;
  font-size: 30px;
}

.hidden {
  background-color: #aad751 !important;
}

.shown {
  background-color:#e5c29f;
}

.shown.mine {
  background-image: url('images/mine.png') !important;
  background-size: contain;
  background-color: red;
}

.shown._1 {
  background-image: url('images/1-blue.png');
  background-size: contain;
}

.shown._2 {
  background-image: url('images/2-green.png');
  background-size: contain;
}

.shown._3 {
  background-image: url('images/3-red.png');
  background-size: contain;
}

.marked {
  background-image: url('images/flag_icon.png');
  background-size: contain;
}

.timer-button {
  width: 80px;
  height: 30px;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 20px;
  border-radius: 3px;
  border-color:  #dea5a4;
}

.difficulty-button {
  width: 100px;
  height: 40px;
  margin: 15px;
  padding: 5px;
  border-radius: 3px;
  border-color:  #dea5a4;
}

.title-minesweeper {
  color: #dea5a4;
  margin-bottom: 0px;
}

#timer-wrapper {
  display: flex;
}